import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  text = [
    'Hey Bodile!',
    'Als eerste...',
    'GEFELICITEERD!!!',
    '(nogmaals)',
    'Dus nu vraag je je zeker af waarom je hierheen moest komen?',
    'Waarschijnlijk heb je al een idee...',
    'Als eerste heb ik een vraag bedacht.',
    'Wat vond je van de plantjes?',
    'Hopelijk leuk!',
    'Want...',
    'Er is meer!',
    'Namelijk:',
    '.',
    '. .',
    '. . .',
    '',
    'EEN DAGJE UIT NAAR DE EFTELING!!!',
    '(Wanneer het wat rustiger is)',
    ':)',
    'Dus ja dat was het eigenlijk wel',
    'Verder is niet meer zo veel.',
    '',
    'Of misschien toch wel!',
    'Nee grapje, dit was hem.',
  ]

  currentIndex = 0
  currentText = this.text[this.currentIndex]
  letters = '0123456789ABCDEF'
  color = ''
  color2 = ''

  constructor() {
    this.getRandomColors()
  }

  getRandomColors() {
    this.color = '#';
    this.color2 = '#'; 
    for (var i = 0; i < 6; i++) {
        this.color += this.letters[Math.floor(Math.random() * 16)];
        this.color2 += this.letters[Math.floor(Math.random() * 16)];
    }
}

  nextLine(): void {
    if (this.currentIndex == this.text.length-1) 
      return 
    this.currentIndex += 1
    this.currentText = this.text[this.currentIndex]
    this.getRandomColors()
  }

  previousLine(): void {
    if (this.currentIndex == 0) 
      this.currentIndex -= 1
    else 
      this.currentIndex -= 2
    this.currentText = this.text[this.currentIndex]
  }
  
  sameLine(): void {
    this.currentIndex -= 1
  }
}
